	export default {
		data() {
			return {
				mac:'00E0162F0005',
				hex:'',	//设备的hex的信息
				keyResylt:'',	//密钥
			}
		},
		watch:{
			hex(){	//监听设备hex信息改变，随机生成1~5数字，执行对应算法，获取加密密钥
				let x = Math.floor(Math.random() * 5) + 1;	
				/* let fun = 'macAES_'+ x;
				this.fun();	//选择相应算法执行 */
				this.keyResylt = this.AES_API.macAES_5(this.hex);
			},
			keyResylt(){
				console.log('密钥为：'+this.keyResylt);
				let EncryptResult = this.AES_API.getEncryptResult(this.keyResylt,'#PW=9527');
				console.log("加密结果："+ EncryptResult); 
				const arrayBuffer = new Uint8Array(Buffer.from(EncryptResult, 'hex'));	//将hex转化为arrayBuffer
				const base64 = uni.arrayBufferToBase64(arrayBuffer);	//将arrayBuffer转化为base64
				let DecryptResult = this.AES_API.getDecryptResult(this.keyResylt,base64);
				console.log("解密结果："+ DecryptResult);
			},
		},
		methods: {
			judgeBit(strBit){ //如果该字节转化为16进制长度为1，则前面补0
				if(strBit.length == 1){
					return '0'+strBit;
				}else{
					return strBit;
				}
			},
			getsTR(){
				console.log("3333333333")
				let encrypyStr = 'KQpZ/jeLV0wasdw6DXDD8w==';
				let ArrayBuffer = Buffer.from(encrypyStr, 'base64');
				console.log(ArrayBuffer)
				let result = '';
				for(let k = 0; k < ArrayBuffer.length ; k++){
					result += this.judgeBit(ArrayBuffer[k].toString(16));
				} 
				console.log('16进制：'+result); 
				let str = Buffer.from(result, 'hex');
				console.log(str)
				let strResult = '';
				for(let i = 0; i < str.length ; i++){
					strResult += String.fromCharCode(str[i]);
				} 
				console.log('字符串：'+strResult);
			},
			Str2Bytes(str) {
				var pos = 0;
				var len = str.length;
				if(len %2 != 0) {
					return null;
				}
				len /= 2;
				var hexA = new Array();
				for(var i=0; i<len; i++) {
					var s = str.substr(pos, 2);
					var v = parseInt(s, 16);
					hexA.push(v);
					pos += 2;
				}
				return hexA;
			},
			arrayBufferToBase64( buffer ) {
				var binary = '';
				var bytes = new Uint8Array( buffer );
				var len = bytes.byteLength;
				for (var i = 0; i < len; i++) {
					binary += String.fromCharCode( bytes[ i ] );
				}
//                return window.btoa( binary );
				return binary;
			 },
		},
		onLoad() {
			// this.hex = this.getCRC16_IBM(this.mac);	//获取CRC16-IBM校验值
			this.hex = this.AES_API.getCRC16_IBM(this.mac);	//获取CRC16-IBM校验值
		},
	}
